@import "./../styles/variables.scss";
@import url('https://fonts.googleapis.com/css2?family=Actor&display=swap');


.header {
    background: #163760;
    width: 100%;
    height: 115px;
    /*position: fixed;
    z-index: 100;*/
    padding: 0 1.5rem;
    @media (max-width: 390px) {
        height: 70px;
    }
    @include breakpoint(md) {
        padding: 0 3rem;
    }
    transition: 0.3s ease all;

    &__content {
        overflow: hidden;
        color: $light;
        margin: 0 auto;
        max-width: 1920px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        z-index: 100;


        &__logo img {           
           display: flex;
           align-items: center;
           justify-content: center;
           
           width: 55px;
            height: 65px;
           @include breakpoint(md) {
            width: 7rem;
            height: 6.5rem;
        }
        
        }

        &__nav {
            top: 0;
            right: 100%;
            bottom: 0;
            width: 100%;
            height: 100vh;
            position: fixed;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-align: center;
            background: rgba(#D9D9D9, 0.9);
            backdrop-filter: blur(2px);
            transform: translate(0);
            transition: 0.3s ease transform;
            @media (max-width: 390px) {
                color: #163760;
            font-family: 'Actor';
            font-style: normal;
            font-weight: 600;
            font-size: 15.9747px;
            line-height: 35px;       
            text-align: center;
            text-decoration-line: underline;
            }

            @include breakpoint(md) {
                transform: none;
                flex-direction: row;
                background: transparent;
                width: auto;
                height: 100%;
                position: static;
            }
            ul {
                list-style: none;
                padding: 0;
                font-size: 1.25rem;
                font-weight: 400;
                display: flex;
                flex-direction: column;
                margin-bottom: $spacing-lg;
                @include breakpoint(md) {
                    flex-direction: row;
                    align-items: center;
                    margin-bottom: 0;
                    margin-right: calc(0.5rem + #{$spacing-md});
                }
                li {
                    &:not(:last-child) {
                        margin-bottom: $spacing-lg;
                        @include breakpoint(md) {
                            margin-bottom: 0;
                            margin-right: $spacing-md;
                        }
                    }
                    a {
                        text-decoration: none;
                        color: inherit;
                        padding: 0.75rem 1.25rem;
                        border-radius: $borderRadius;
                        transition: 0.3s ease all;

                        &:hover {
                            background: rgba($light, 0.1);
                        }
                        &:active {
                            border-radius: calc(#{$borderRadius} + 6px);
                            background: linear-gradient(rgba($light, 0.1), rgba($light, 0.2));
                        }
                    }
                }
            }
            button {
                cursor: pointer;
                outline: none;
                padding: 0.25rem .75rem;
               
                border-radius: 21.1655px; 
                padding: 0.20rem 1.5rem;                               
                font-size: 1.10rem;
                font-weight: 400;
                font-family: inherit;
                background: #FCFF69;
                color: #000000;
                border: 2.53986px solid #FFFFFF;
                transition: 0.3s ease all;
                @include breakpoint(md) {
                    margin-left: 2rem;
                }

                &:hover {
                    opacity: 0.9;
                    border: 2.53986px solid #fff;
                }

                &:active {
                    filter: brightness(1.2);
                    border: 2.53986px solid #FF0000;
                }
            }

            &.isMenu {
                transform: translate(100%);
            }
        }

        &__toggle {
            cursor: pointer;
            display: flex;
            align-items: center;
            font-size: 2rem;
            transition: 0.3s ease all;
            position: relative;

            &:hover {
                color: $primary;
            }

            @include breakpoint(md) {
                display: none;
            }
        }
    }
}
